  const getMax = (arr, prop) => {
    var maxItem;
    for (var i = 0; i < arr.length; i++) {
        if (maxItem == null || parseInt(arr[i][prop]) > parseInt(maxItem[prop]))
          maxItem = arr[i];
    }
    return maxItem[prop];
}

  /* Fuction handle business call by upload any Excel file */
  const handleFileUpload = (file) => {
    console.log(file);
    /* disable export button */
    setDisableExportButton(true);

    if (!file) return;
    setFileName(file.name);
    const reader = new FileReader();

    reader.onload = (e) => {
      /* Read Excel file */
      const data = e.target.result;
      const workbook = read(data, { type: 'binary' });
      const sheetName = workbook.SheetNames[0];
      /* Get the first sheet as INPUT */
      const worksheet = workbook.Sheets[sheetName];

      if (worksheet != null) {
        /* Get all rows of work sheet as JSON */
        const header = utils.sheet_to_json(worksheet, { header: 1 })[0];
        console.log("\n--- header ---");
        console.log(header);
        const allRows = utils.sheet_to_json(worksheet);
        console.log("\n--- allRows ---");
        console.log(allRows);

        /* Create array result for saving result output */
        var results = [];

        /* Declare label of cols in Excel from header () */
        const col_A = header[0]; // Level
        const col_C = header[2]; // Part Number

        /* Browse all elements of allRows */
        if (allRows != null && allRows.length > 0) {
          /* Get max of Level */
          const maxLevel = getMax(allRows, col_A);
          console.log("\n--- maxLevel ---");
          console.log(maxLevel);

          /* Create level array contain all levels */
          const levelArr = [];
          /* Create header for each level */
          var headerLevel = [];
          /* Create map to storage PN of each level */
          const map_lv_PN = new Map();
          for (var i = 1; i <= maxLevel; i++) {
            levelArr.push(i);
            headerLevel.push("PN Parent Level " + i);
            map_lv_PN.set(i, "");
          }
          console.log("\n--- levelArr ---");
          console.log(levelArr);
          console.log("\n--- headerLevel ---");
          console.log(headerLevel);

          allRows.forEach((row, index) => {
            let copiedRow = JSON.parse(JSON.stringify(row));
            levelArr.forEach((level, lv_i) => {
              let row_level_string = copiedRow[col_A];
              if (row_level_string === "") {
                var row_level = 0;
              } else {
                row_level = parseInt(row_level_string);
              }
              if (level === row_level) {
                /* If current row level === one level (example level 3)
                - keep the PN values of map before level 3,
                - change the PN value of level 3 by row[PN], (1)
                - then set the PN value after level 3 to EMPTY (2)*/
                map_lv_PN.set(level, row[col_C]); // (1)
                for (var lv = level + 1; lv <= maxLevel; lv++) {
                  map_lv_PN.set(lv, "");// (2)
                }
                return;
              }
            });

            /* Adding columns headerLevel into results row*/
            for (var lvl = 1; lvl <= maxLevel; lvl++) {
              // copiedRow[headerLevel[lvl - 1]] = map_lv_PN.get(lvl);
              copiedRow = {...copiedRow, [headerLevel[lvl - 1]]: map_lv_PN.get(lvl)};
            }
            results.push(copiedRow);
          });
        }

        /* ./Browse all elements of rows */
        console.log("\n--- results ---");
        console.log(results);
        console.log("\n--- OUTPUT ---");
        if (results != null && results.length > 0) {
          var outputHeader = [...header, ...headerLevel];
          console.log("\n--- outputHeader ---");
          console.log(outputHeader);

          var rowOutputTmp;
          var resultForOUTPUTWorksheet = [];
          results.forEach((r, i) => {
            rowOutputTmp = {};
            outputHeader.forEach((h, h_ind) => {
              rowOutputTmp[h] = r[h] != null ? r[h] : "";
            });
            resultForOUTPUTWorksheet.push(rowOutputTmp);
          });
          console.log(resultForOUTPUTWorksheet);
          var outputWorksheet = utils.json_to_sheet(resultForOUTPUTWorksheet);
  
          /* Set columns width fit content for Worksheet */
          // autofitColumns(results, outputWorksheet);
          /* Add formatting for Worksheet */
          // set_style_for_columns(outputWorksheet);
  
  
          var worksheetName = workbook.Sheets["OUTPUT"] == null ? "OUTPUT" : "OUTPUT__" + dateTime();
          utils.book_append_sheet(workbook, outputWorksheet, worksheetName);

          console.log(" ===> [OUTPUT] sheet is created successfully!!!");
          message.success("[OUTPUT] sheet is created successfully!!!");
        } else {
          var r__NO_CHANGE= {
            "": "Nothing"
          }
          outputWorksheet = utils.json_to_sheet([r__NO_CHANGE]);

          worksheetName = workbook.Sheets["OUTPUT"] == null ? "OUTPUT" : "OUTPUT__" + dateTime();
          utils.book_append_sheet(workbook, outputWorksheet, worksheetName);

          console.log(" ===> [OUTPUT] sheet: Nothing!!!");
          message.info("[OUTPUT] sheet: Nothing!!!");
        }

        setGlobalWorkbook(workbook);
        // ./Excel manipulation
        /* enable export button */
        setDisableExportButton(false);
      } else {
        console.log("Input Excel File must contain AT LEAST ONE worksheet!!!");
        message.error("Input Excel File must contain AT LEAST ONE worksheet!!!");
      }

    };

    reader.readAsBinaryString(file);
  };

